<style scoped>
    .pages {
        text-align: left;
        padding: 0 20px;

    }

    .form {
        width: 100%;
        margin-top: 30px;
    }
</style>

<template>
    <div class="pages">
        <H3>基础信息</H3>
        <div class="line"></div>
        <el-form class="form" ref="form" :model="form" label-width="120px">
            <el-form-item label="活动名称">
                <el-input style="width: 500px" v-model="form.title" placeholder="请输入"></el-input>
            </el-form-item>

            <el-form-item label="活动栏目">
                <el-select v-model="form.column_id" placeholder="请选择">
                    <el-option
                            v-for="item in columns"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="封面图">
                <image-select v-model="img" :url="form.picture"></image-select>
            </el-form-item>
            <el-form-item label="动态简介">
                <el-input
                        type="textarea"
                        style="width: 400px"
                        :rows="5"
                        v-model="form.description" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="推荐">
                <el-select v-model="form.recommend" placeholder="请选择">
                    <el-option
                            :value="1"
                            label="是">
                    </el-option>
                    <el-option
                            :value="0"
                            label="否">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="状态">
                <el-select v-model="form.status" placeholder="请选择">
                    <el-option
                            :value="1"
                            label="启用">
                    </el-option>
                    <el-option
                            :value="0"
                            label="禁用">
                    </el-option>
                </el-select>
            </el-form-item>
            <div>
                <rich-view v-model="form.rich_text"></rich-view>
            </div>
            <el-form-item style="margin-top: 100px">
                <el-button type="primary" style="margin-left: 50px" :loading="isLoading"
                           @click="onSubmit">提交
                </el-button>
                <el-button @click="back">返回</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import api from '../../js/api/dynamic'
    import columnApi from '../../js/api/column'

    export default {
        data() {
            return {
                form: {
                    id: null,
                    title: null,
                    description: null,
                    picture: null,
                    column_id: null,
                    recommend: 0,
                    status: 0,
                    rich_text: null,
                },
                isLoading: false,
                columns: [],
                img: null,
            }
        },
        mounted() {
            if (this.$route.query.id !== undefined) {
                this.loadData();
            }
            this.loadColumn()
        },
        methods: {
            loadColumn() {
                columnApi.index({page_size: 999}, res => {
                    this.columns = res.data.data.data;
                })
            },
            loadData() {
                api.detail({id: this.$route.query.id}, res => {
                    this.form = res.data.data;
                })
            },

            edit() {
                this.isNoEdit = false;
            },
            onSubmit() {
                if (!api.isStrValid(this.form.title)) {
                    this.$message.error('请输入动态名称');
                    return;
                }
                if (!api.isStrValid(this.form.column_id)) {
                    this.$message.error('请选择栏目');
                    return;
                }
                if (this.img == null && this.form.picture == null) {
                    this.$message.error('请选择图片');
                    return;
                }
                if (!api.isStrValid(this.form.description)) {
                    this.$message.error('请输入动态简介');
                    return;
                }


                if (!api.isStrValid(this.form.rich_text)) {
                    this.$message.error('请输入活动内容富文本');
                    return;
                }
                let data = api.objToFormData(this.form);
                data.append('picture', this.img);
                this.isLoading = true;
                api.save(data, res => {
                    this.isLoading = false;
                    this.$message.success(res.data.message);
                    this.$router.push({name: 'dynamic'});
                    this.$route.meta.keepAlive = false;
                }, () => {
                    this.isLoading = false;
                })

            },
            back() {
                this.$router.back();
            }

        }
    }
</script>
